    box-sizing: border-box;
}

body {
    margin: 0;
}

/* ------- HEADER ------- */

#header {
    height: 10svh;
    background-color: #222;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
}

.logo {
    font-size: 2.5rem;
    font-style: italic;
    font-weight: bold;
    color: white;
}

#search-icon {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

/* ------- CURRENT WEATHER ------- */

.today-info {
    height: 60svh;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-info {
    min-width: 300px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#location {
    width: 100%;
    line-height: .5rem;
}

#city {
    font-size: 1.8rem;
    font-weight: bold;
}

#country,
#current-condition {
    font-size: 1.3rem;
}

.main-weather {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#current-temperature {
    font-size: 3.5rem;
    line-height: 0;
}

#current-condition-icon {
    width: 30%;
}

.hourly-info {
    height: 100%;
    width: 1000px;
    overflow: scroll;
    border-top: 1px solid #BBB;
    border-bottom: 1px solid #BBB;
    display: flex;
    gap: 5rem;
}

.weather-per-hour {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    justify-items: center;
    align-items: center;
}

/* ------- ADDITIONAL INFORMATION ------- */

.additional-info {
    height: 30svh;
    background-color: #777;
    padding: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 1rem;
}

.info {
    text-align: center;
}

.info > p {
    color: #E8E8E8;
    font-size: 1rem;
}



/* ------- FORECAST ------- */

.forecast {
    padding: 2rem;
    display: grid;
    grid-template-rows: repeat(3, min-content);
}

.forecast .header,
.daily-forecast {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: center;
    align-items: center;
}

.forecast .header {
    align-items: end;
    border-bottom: 1px solid #bdbdbd;
}

#forecast-day,
.weekday {
    justify-self: start;
}

.forecast .temperature {
    width: 100%;
    text-align: center;
}

.min-max {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.daily-forecast-temperature {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;AACb;;AAEA,2BAA2B;;AAE3B;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,mDAAoC;AACxC;;AAEA,oCAAoC;;AAEpC;IACI,aAAa;IACb,aAAa;IACb,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,8BAA8B;AAClC;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;;IAEI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,gBAAgB;IAChB,0BAA0B;IAC1B,6BAA6B;IAC7B,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA,2CAA2C;;AAE3C;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,cAAc;IACd,eAAe;AACnB;;;;AAIA,6BAA6B;;AAE7B;IACI,aAAa;IACb,aAAa;IACb,0CAA0C;AAC9C;;AAEA;;IAEI,aAAa;IACb,qCAAqC;IACrC,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,WAAW;IACX,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;AACvB","sourcesContent":["* {\n    box-sizing: border-box;\n}\n\nbody {\n    margin: 0;\n}\n\n/* ------- HEADER ------- */\n\n#header {\n    height: 10svh;\n    background-color: #222;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 2rem;\n}\n\n.logo {\n    font-size: 2.5rem;\n    font-style: italic;\n    font-weight: bold;\n    color: white;\n}\n\n#search-icon {\n    background: url('./search_icon.svg');\n}\n\n/* ------- CURRENT WEATHER ------- */\n\n.today-info {\n    height: 60svh;\n    padding: 2rem;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.main-info {\n    min-width: 300px;\n    height: 100%;\n    display: flex